.offcanvas-header {
  display: flex;
  align-items: center;
  padding: var(--bs-offcanvas-padding);
}
.offcanvas-header .btn-close {
  margin-left: auto;
}

.offcanvas-title {
  margin-bottom: 0;
  line-height: var(--bs-offcanvas-title-line-height);
}

.offcanvas-header {
  padding-bottom: 2rem;
}

.offcanvas-body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: var(--bs-offcanvas-padding);
  padding-top: 2rem;
  overflow-y: auto;
}

@media (min-width: 991.98px) {
  .offcanvas-navbar {
    --bs-offcanvas-width: initial;
    --bs-offcanvas-padding: 0;
    flex: 1;
    box-shadow: unset;
    position: static;
    visibility: visible;
  }
}
@media (min-width: 991.98px) {
  .offcanvas-navbar.offcanvas-start, .offcanvas-navbar.offcanvas-end {
    transform: unset;
  }
}
@media (min-width: 991.98px) {
  .offcanvas-navbar .offcanvas-header {
    display: none;
  }
}
@media (min-width: 991.98px) {
  .offcanvas-navbar .offcanvas-body {
    flex-direction: row;
    padding-top: 0;
    overflow: initial;
  }
}